import React from 'react'


export const ArrowUp = ({upTheTusk,counter}) => {
    return (
    <div onClick={upTheTusk.bind(this, counter)}>

    <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-chevron-compact-up" fill="currentColor"
                xmlns="http://www.w3.org/2000/svg">
        <path fill-rule="evenodd"
              d="M7.776 5.553a.5.5 0 0 1 .448 0l6 3a.5.5 0 1 1-.448.894L8 6.56 2.224 9.447a.5.5 0 1 1-.448-.894l6-3z"/>
    </svg></div>)
}